import { React, useEffect, useState } from 'react';
import { MatchDetailCard } from '../components/MatchDetailCard';
import { MatchSmallCard } from '../components/MatchSmallCard';
import { useParams, Link } from 'react-router-dom';
import { WinLossComponent } from '../components/WinLossComponent';

import './TeamPage.scss';

export const TeamPage = () => {
  const [team, setTeam] = useState({ matches: [] });
  const { teamName } = useParams();

  useEffect(() => {
    const fetchTeam = async () => {
      const response = await fetch(`/team/${teamName}`);

      const data = await response.json();

      setTeam(data);
    };

    fetchTeam();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [teamName]);

  if (!team || !team.teamName) {
    return <h1>Team Not Found!</h1>;
  }

  return (
    <div className='TeamPage'>
      <div className='team-name-section'>
        <h1 className='team-name'>{team.teamName}</h1>
      </div>
      <div className='win-loss-section'>
        <WinLossComponent team={team} />
      </div>

      <div className='match-detail-card-section'>
        <h3>Latest Matches</h3>
        <MatchDetailCard teamName={team.teamName} match={team.matches[0]} />
      </div>

      {team.matches.slice(1).map((match) => (
        <MatchSmallCard key={match.id} teamName={team.teamName} match={match} />
      ))}

      <div className='more-link'>
        <Link
          to={`/team/${teamName}/matches/${process.env.REACT_APP_DATA_END_YEAR}`}
        >
          More {`>`}
        </Link>
      </div>
    </div>
  );
};
